// Main Stylesheet

// Colors
$primary-color: #aaa;
$secondary-color: #000;
$accent-color: #F10CBA;
$shade-color: #f2f2f2;
$complementary-color: 2f2f2f;

// Fonts
$body-font: arial, sans-serif;
$heading-font: times new roman, sans-serif;
$body-font-size: 1rem;

body {
  color: $primary-color;
}

@import
  "_resets",
  "_utility",
  "_universal";